import React, { useRef } from "react";
import GoogleMapReact from "google-map-react";
import { useTranslation } from "react-i18next";
import _ from "lodash";

import mapKey from "../Components/key";
// import { token } from "../Components/api/token";
import { mailUrl } from "../Components/api/mail_url";
import { Layout } from "../Components/Layout";
import { useFormValidate } from "../Components/useFormValidate";

const Contact = () => {
  const { t } = useTranslation();

  const emailRule =
    /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
  const isNotEmpty = (value) => value.trim() !== "";
  const isEmailFormat = (value) => emailRule.test(value);

  const {
    isValid: nameIsValid,
    hasError: nameError,
    onChangeValue: onChangeName,
    onBlurValue: onBlurName,
  } = useFormValidate(isNotEmpty);

  const {
    isValid: emailIsValid,
    hasError: emailError,
    onChangeValue: onChangeEmail,
    onBlurValue: onBlurEmail,
  } = useFormValidate(isEmailFormat);

  const {
    isValid: subjectIsValid,
    hasError: subjectError,
    onChangeValue: onChangeSubject,
    onBlurValue: onBlurSubject,
  } = useFormValidate(isNotEmpty);

  const {
    isValid: contentIsValid,
    hasError: contentError,
    onChangeValue: onChangeContent,
    onBlurValue: onBlurContent,
  } = useFormValidate(isNotEmpty);

  //  CSRF token
  // useEffect(() => {
  //   fetch(token)
  //     .then((response) => response.text())
  //     .then((csrfToken) => {
  //       //  CSRF  meta 
  //       const metaTag = document.querySelector('meta[name="csrf-token"]');
  //       if (metaTag) {
  //         metaTag.setAttribute("content", csrfToken);
  //       }
  //     })
  //     .catch((error) => {
  //       console.error(" CSRF :", error);
  //     });
  // }, []);

  // CSRF
  // const csrfToken = document
  //   .querySelector('meta[name="csrf-token"]')
  //   .getAttribute("content");

  const form = useRef();

  // email
  async function sendEmail(e) {
    e.preventDefault();

    // 
    if (!nameIsValid) {
      alert("");
      return;
    } else if (!emailIsValid) {
      alert("");
      return;
    } else if (!subjectIsValid) {
      alert("");
      return;
    } else if (!contentIsValid) {
      alert("");
      return;
    }

    try {
      //  token
      // const response = await fetch(token);
      // const _token = await response.text();
      // console.log(_token);

      // 
      const formData = new FormData(form.current);
      const requestBody = Object.fromEntries(formData.entries());

      // 
      await fetch(mailUrl, {
        method: "POST",
        body: JSON.stringify(requestBody),
        headers: {
          "Content-type": "application/json",
          // "X-CSRF-TOKEN": csrfToken,
        },
      })
        .then((response) => response.json())
        .then((data) => {
          console.log(data);
          alert("");
          form.current.reset();
        });
    } catch (error) {
      console.error(":", error);
      alert("");
    }
  }

  const throttledSendEmail = _.throttle(sendEmail, 2000);

  const nameInputClasses = nameError ? "invalid" : "";
  const emailInputClasses = emailError ? "invalid" : "";
  const subjectInputClasses = emailError ? "invalid" : "";
  const contentInputClasses = emailError ? "invalid" : "";

  // google
  const CustomMarker = ({ text }) => (
    <div className="markPoint">
      <div
        className="point"
        style={{
          width: "50px",
          color: "#f00",
          fontSize: "3em",
          position: "absolute",
          top: "-40px",
        }}
      >
        <i className="fa fa-map-marker" aria-hidden="true"></i>
      </div>
      <div
        className="text"
        style={{
          width: "140px",
          color: "#f00",
          fontSize: "1.3em",
          fontWeight: "600",
          position: "absolute",
          top: "-40px",
          left: "20px",
        }}
      >
        {text}
      </div>
    </div>
  );

  return (
    <Layout>
      <div className="map">
        <GoogleMapReact
          bootstrapURLKeys={{ key: mapKey }}
          defaultCenter={{ lat: 24.998752, lng: 121.4247906 }}
          defaultZoom={11}
          yesIWantToUseGoogleMapApiInternals
        >
          <CustomMarker
            lat={24.9553234}
            lng={121.3841572}
            text=""
          />
          <CustomMarker lat={25.0480859} lng={121.469599} text="" />
        </GoogleMapReact>
      </div>

      {/*  */}
      <form ref={form} onSubmit={throttledSendEmail} className="contact-form">
        <div className="form-wrap">
          <label>
            {t("")}{" "}
            <span>
              *
              {nameError && (
                <p className="error-text">{t("")}</p>
              )}
            </span>
          </label>
          <input
            type="text"
            name="user_name"
            placeholder={t("")}
            onChange={onChangeName}
            onBlur={onBlurName}
            className={nameInputClasses}
          />

          <br />
        </div>

        <div className="form-wrap">
          <label>
            {t("")}{" "}
            <span>
              *
              {emailError && (
                <p className="error-text">{t("")}</p>
              )}
            </span>
          </label>
          <input
            type="email"
            name="email"
            placeholder={t("")}
            onChange={onChangeEmail}
            onBlur={onBlurEmail}
            className={emailInputClasses}
          />

          <br />
        </div>

        <div className="form-wrap">
          <label>
            {t("")}{" "}
            <span>
              *
              {subjectError && (
                <p className="error-text">{t("")}</p>
              )}
            </span>
          </label>
          <input
            type="text"
            name="subject"
            placeholder={t("")}
            onChange={onChangeSubject}
            onBlur={onBlurSubject}
            className={subjectInputClasses}
          />

          <br />
        </div>

        <div className="form-wrap">
          <label>
            {t("")}{" "}
            <span>
              *
              {contentError && (
                <p className="error-text">{t("")}</p>
              )}
            </span>
          </label>
          <textarea
            name="message"
            cols="30"
            rows="10"
            onChange={onChangeContent}
            onBlur={onBlurContent}
            className={contentInputClasses}
          />
        </div>

        {/* <input type="hidden" name="_token" value={csrfToken} /> */}

        <div className="form-wrap">
          <input
            type="submit"
            value={t("")}
            className="submit"
            disabled={!nameIsValid || !emailIsValid}
          />
        </div>
      </form>
    </Layout>
  );
};

export default Contact;
